import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

import 'aframe-particle-system-component';
import 'aframe-environment-component';

import { Scene, Entity } from 'aframe-react';
import { Button } from 'react-bootstrap';

const styles = {
  backButton: {
    zIndex: 1000,
    position: 'absolute',
    right: '1rem',
    top: '1rem',
  },
  backButton2: {
    zIndex: 1000,
    position: 'absolute',
    right: '7.5rem',
    top: '1rem',
  },
  backButton3: {
    zIndex: 1000,
    position: 'absolute',
    right: '14rem',
    top: '1rem',
  }
}

class App extends Component {
  render() {
    return (
      <div className="App">
        <Button style={styles.backButton} bsStyle="primary">Zoom +</Button>
        <Button style={styles.backButton2} bsStyle="primary">Zoom -</Button>
        <Button style={styles.backButton3} bsStyle="primary">Zoom -</Button>
        <Scene artoolkit={{ debugUIEnabled: false, sourceType: 'webcam', trackingMethod: 'best' }}>
          <a-assets>
            <a-asset-item id="model" src="https://raw.githubusercontent.com/kalmykov/fr24-3d-models/master/models/b748.glb" crossOrigin="anonymous"></a-asset-item>
          </a-assets>
          <a-marker-camera preset='hiro'>
            <a-box position='0 0.5 0' material='color: red;' event-set__click="material.color: red; scale: 2 2 2"></a-box>
            <a-gltf-model scale="0.1 0.1 0.1" src="#model"></a-gltf-model>
          </a-marker-camera>
          <Entity camera></Entity>
        </Scene>
      </div>
    );
  }
}
export default App;



